export default {
	props: {
		show: {
			type: Boolean,
			default: false
		},
		items: {
			type: Array,
			default: () => {
				return []
			}
		}
	},
	data() {
		return {

		}
	},
	methods: {
		select(item) {
			this.$emit("selectItem", {
				item: item,
				show: false
			});
		},
		close(){
			this.$emit("selectItem",{
				item: null,
				show:false
			});
		}
	}
}
